import Scroll from 'base/scroll/scroll'

export default {
  name: 'search-list',
  mixins: [],
  props: {
    searchHistory: {
      type: Array,
      default: []
    }
  },
  data () {
    return {}
  },
  computed: {},
  components: {
    Scroll
  },
  watch: {},
  methods: {
    deleteSearchHistory (item) {
      this.$emit('delete', item)
    },
    searchHistoryItem (item) {
      this.$emit('select', item)
    }
  },
  created () {},
  mounted () {}
}



