
import List from 'base/list/list'
import Scroll from 'base/scroll/scroll'
import VHeader from 'components/v-header/v-header'
import SearchList from 'base/search-list/search-list'
import SearchBox from 'base/search-box/search-box'
import Confirm from 'vux/src/components/confirm/index.vue'

import { getSearch } from 'api'
import { ERR_OK } from 'api/config'
import { mapActions, mapGetters } from 'vuex'
import { listLoadMixin } from 'common/js/mixin'

export default {
name: 'search',
mixins: [listLoadMixin],
data () {
return {
list: [],
page: 1,
hasMore: true,
pullup: true,
isShowBlock: false,
query: '',
isShowLoading: false,
listenScroll: true,
probeType: 2,
hasResult: true,
isShowClear: false
}
},
computed: {
...mapGetters([
'searchHistory'
])
},
components: {
List,
Scroll,
Confirm,
VHeader,
SearchList,
SearchBox
},
watch: {
query (newQuery, oldQuery) {
if (!newQuery) {
this.clearQuery()
this.$nextTick(() => {
this.$refs.searchHistory.refresh()
})
}
}
},
methods: {
showClearConfirm () {
this.isShowClear = true
},
onQueryChange (query) {
this.query = query
},
blurInput () {
this.$refs.searchBox.blur()
},
clearQuery () {
this.list = []
this.page = 1
this.query = ''
this.hasSearchResult(true)
this.showLoading(false)
},
_setQuery (query) {
this.$refs.searchBox.setQuery(query)
},
searchQuery (query) {
if (!query) return
this.list = []
this.page = 1
this.query = query
this.hasSearchResult(true)
this._getSearch()
},
selectAndSearchHistory (query) {
this._setQuery(query)
this.searchQuery(query)
},
loadMore () {
this.page += 1
const params = {
'query.q': this.query,
limit: 15,
currentPage: this.page
}
getSearch(params).then(data => {
if (data.code === ERR_OK) {
this.list = this.list.concat(data.list)
}
})
},
selectItem (item) {
this.fetchDetail(item)
},
_getSearch () {
const params = {
'query.q': this.query,
limit: 15,
currentPage: this.page
}
this.saveSearchHistory(this.query)
this.showLoading(true)
getSearch(params).then(data => {
if (data.code === ERR_OK) {
this.list = data.list
this.showLoading(false)
if (!data.list.length) this.hasSearchResult(false)
}
})
},
hasSearchResult (boolean) {
this.hasResult = boolean
},
showLoading (boolean) {
this.isShowLoading = boolean
},
...mapActions([
'fetchDetail',
'saveSearchHistory',
'clearSearchHistory',
'deleteSearchHistory'
])
},
created () {
},
mounted () {
}
}



